/* General container styles */
.pricing-container {
  text-align: center;
  padding: 50px;
  background-color: #f9f9f9;
  color: #333;
}

/* Title */
.pricing-title {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #4a4a4a;
}

/* Card container */
.pricing-cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

/* Individual card styles */
.pricing-card {
  background: white;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 20px;
  width: 300px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.pricing-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

/* Plan title */
.plan-title {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: #2d89ff;
}

/* Plan price */
.plan-price {
  font-size: 1.8rem;
  margin: 15px 0;
  font-weight: bold;
  color: #333;
}

/* Features list */
.plan-features {
  list-style: none;
  padding: 0;
  margin: 15px 0;
}

.plan-features li {
  font-size: 1rem;
  margin: 10px 0;
  color: #555;
}

/* Button */
.choose-plan-btn {
  background-color: #2d89ff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.choose-plan-btn:hover {
  background-color: #1c63d8;
}
